@media screen and (max-width: 768px) {
body{
  background-size:contain;
  background-color:tan;
}
.full{
    display: flex;
    flex-wrap: wrap;
    margin:0;
    padding:0;
    padding-top:30%;
    background-image: url("../images/rose-egg.svg");
    background-position: top;
    background-size: cover;
 
    
  }
  .full > a{
    color:navy;
    font-size:4em;
    width: 100%;
    margin:0;
  }
  .welcome{
    display:none;
   
}
.logo{
    background-color:deeppink;
}

.iconsnav{
  width:100%;
  background-color: rgba(0, 0, 0, 0.4);
}
.iconsnav, .navTop{
    position: fixed;
    top:0;
    margin:0;
    z-index:1;
}
.iconsnav img{
    width: 15%;
    height: 15%;
  }
.iconsnav a:last-child{
  font-size: 1em;
  }
.navTop{
  background-color: mediumseagreen;
  width:100%;
  padding: 5px;
  justify-content: space-evenly;
}
.navTop a{
  float:right;
}
.navTop img{
  font-size: 15px;
  margin:0;

}
.back-arrow::before{
  top: -12px;
  left:-50%;
  font-size: 4em;
}
.aboutbox-article{
  display: block;
  margin:0;
}
.aboutbox p{
  padding: 10px;
  margin: 0;
}
.date{
  display: none;
}
.projects li:nth-child(6) {
      font-size: 2em;
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      padding: 30px;
    }
    .projects li a {
      padding: 15px;
      border: 2px solid white;
      border-radius: 50%;
      width: 80px;
      height: 80px;
      display: grid;
      place-content: center;
    }
    ul span {
      display: none;
    }
  .button i{
    font-size: 3em;
  }
  .collab{
    display:none;
  }
  .contact{
    display:none;
  }
  }