@import url(https://fonts.googleapis.com/css?family=Pacifico|Cabin+Sketch|Montserrat+Subrayada|Mansalva|Josefin+Slab|Fredericka+the+Great|Indie+Flower|Caveat|Lobster|Quicksand|Amatic+SC&display=swap);
.home{
  overflow: hidden;
  width: 100%; 
  font-family: 'Cabin Sketch', cursive;
}
.full {
  height: 100vh;
  display: grid;
  grid-template-rows: repeat (5, 1fr);
  grid-template-columns: auto-fit 1fr 1fr 1fr 600px;
  grid-gap: 15px;
  padding: 0 0 20px 20px;
}
.full > a,
.iconsnav:not(.welcome) {
  /* border-left: 4px black solid;
  border-top: 4px black solid; */
  align-self:center;
  text-align: center;
  color: gold;
  font-size:50px;
  padding:20px;
}
.full > a:hover{
  color: navy;
  -webkit-transition: color 2s;
  transition: color 2s;
}
.aboutme {
  grid-area: 1 / 1 / 4 / 1;
}
.work {
  grid-area: 2 / 2 / 4 / 4;
}
.welcome {
  grid-area: 1 / 4 / 5 / 6;
  background-image: url(/portfolio/static/media/rose-egg.d5dc20f0.svg);
  background-size: contain;
  background-position: right;
  /* background-attachment: fixed; */
  background-repeat: no-repeat;
  overflow: visible;
  display: grid;
  place-items: center;
}
.welcome p {
  font-family: 'Cabin Sketch', cursive;
  color:navy;
  font-size:4em;
  text-align: right;
  border-right: 5px dotted navy;
  padding-right:20px;
  margin:20px;
}
.welcome p:hover{
  color: magenta;
  -webkit-transition: color 2s;
  transition: color 2s;
}
.logo{
  display: none;
}
.iconsnav {
  grid-area: 4 / 1 / 4 / 4;
}
.iconsnav a{
  padding: 0 10px 0 10px;
}
.iconsnav img{
  height: 3em;
}
.iconsnav a:last-child{
  /* color:#FFD700; */
  font-size: 1.5em;
  padding:0;
}
.iconsnav a:last-child:hover{
  color: #000080;
}
.iconsnav img:hover {
  -webkit-filter: invert(15%) sepia(91%) saturate(6288%) hue-rotate(215deg) brightness(56%) contrast(112%);
          filter: invert(15%) sepia(91%) saturate(6288%) hue-rotate(215deg) brightness(56%) contrast(112%);
}
#about{
  display: flow-root;
}
.aboutbox article {
  font-family: 'Fredericka the Great', cursive;
  border-bottom: 1px solid gold;
  text-align: justify;
  padding-bottom: 50px; 
}
.aboutbox>article:not(.bio){
  padding-top: 50px;
}
.bio .date{
float:left;
}
.skills .date{
float:right;
}
.skills>p span{
  color:gold;
}
.date span {
  font-family: "Lobster", cursive;
  font-size: 3em;
  line-height: 90px;
  color: white;
}
.date {
  border: 4px gold solid;
  shape-outside: circle(95px);
  width: 90px;
  height: 90px;
  border-radius: 90px;
  text-align:center;
  margin: 50px;
  /* background-color: gold; */
}


.projects {
  font-family: "Quicksand", sans-serif !important;
}
.projectbox{
  text-align:center;
  margin-top:70px;
}
.projectbox img {
margin-bottom: 30px;
max-width: 100%;
max-height: 100%;
}
h2 {
  font-size: 2em;
  padding-bottom: 15px;
  font-family: "Lobster", cursive;
  color: gold;
  word-break:break-word;
}
.projectbox li {
  padding-bottom: 5px;
}
.projectbox li:nth-child(6) {
  font-size: 1.5em;
  padding-top: 15px;
  border-bottom: 1px solid gold;
}
.button i {
  padding: 15px;
}
.button span {
  padding-left: 2%;
}
.button:hover > span,
i {
  color: navy;
  border: gold;
}
.contact {
  display: flex;
  flex-wrap:wrap;
}
form, .collab {
  flex-basis: 50%;
}
input,
textarea,button {
  width: 40vw;
  border: 2px solid transparent;
  margin: 5px;
  padding: 5px;
  background-color: transparent;
  color: gold;
  font-size: 1em;
  font-family: "Quicksand", sans-serif;
}
textarea {
  height: 20vw;
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: white;
  text-align: left;
}
input::-moz-placeholder,
textarea::-moz-placeholder {
  color: white;
  text-align: left;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: white;
  text-align: left;
}
input::-ms-input-placeholder,
textarea::-ms-input-placeholder {
  color: white;
  text-align: left;
}
input::placeholder,
textarea::placeholder {
  color: white;
  text-align: left;
}
.contact ul li{
  border-bottom: 1px solid gold;
}
button[type="submit"]{
  text-align:left;
}
button[type="submit"] i{
  font-size:2em;
  color:white;
}
button[type="submit"] i:hover {
  color: gold;
  -webkit-transition: color 1s;
  transition: color 1s;
}
.collab{
  align-self:center;
  font-size:3em;
  text-align:center;
  font-family: 'Cabin Sketch', cursive;
}
#next-line-intent{
  text-indent: 2em; 
  font-size:1em;
}
#colorSign{
  color:navy; 
  font-size:1em;
}
.navTop {
    padding: 50px;
    display: flex;
    position: relative;
    background-color: transparent;
    justify-content: flex-end;
    text-align: center;
  }
  .back-arrow::before{
    position: absolute;
    content:"<";
    color: gold;
    font-family: 'Cabin Sketch', cursive;
    font-size: 6em;
    right: 40%;
    top: 0;
  }
  .navTop a {
    padding: 0 3% 0 3%;
  }
  .navTop img{
    height:3em;
  }
  .back-arrow:hover:before{
    color: navy;
  }
  .navTop img:hover {
    -webkit-filter: invert(15%) sepia(91%) saturate(6288%) hue-rotate(215deg) brightness(56%) contrast(112%);
            filter: invert(15%) sepia(91%) saturate(6288%) hue-rotate(215deg) brightness(56%) contrast(112%);
  }
@media screen and (max-width: 768px) {
body{
  background-size:contain;
  background-color:tan;
}
.full{
    display: flex;
    flex-wrap: wrap;
    margin:0;
    padding:0;
    padding-top:30%;
    background-image: url(/portfolio/static/media/rose-egg.d5dc20f0.svg);
    background-position: top;
    background-size: cover;
 
    
  }
  .full > a{
    color:navy;
    font-size:4em;
    width: 100%;
    margin:0;
  }
  .welcome{
    display:none;
   
}
.logo{
    background-color:deeppink;
}

.iconsnav{
  width:100%;
  background-color: rgba(0, 0, 0, 0.4);
}
.iconsnav, .navTop{
    position: fixed;
    top:0;
    margin:0;
    z-index:1;
}
.iconsnav img{
    width: 15%;
    height: 15%;
  }
.iconsnav a:last-child{
  font-size: 1em;
  }
.navTop{
  background-color: mediumseagreen;
  width:100%;
  padding: 5px;
  justify-content: space-evenly;
}
.navTop a{
  float:right;
}
.navTop img{
  font-size: 15px;
  margin:0;

}
.back-arrow::before{
  top: -12px;
  left:-50%;
  font-size: 4em;
}
.aboutbox-article{
  display: block;
  margin:0;
}
.aboutbox p{
  padding: 10px;
  margin: 0;
}
.date{
  display: none;
}
.projects li:nth-child(6) {
      font-size: 2em;
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      padding: 30px;
    }
    .projects li a {
      padding: 15px;
      border: 2px solid white;
      border-radius: 50%;
      width: 80px;
      height: 80px;
      display: grid;
      place-content: center;
    }
    ul span {
      display: none;
    }
  .button i{
    font-size: 3em;
  }
  .collab{
    display:none;
  }
  .contact{
    display:none;
  }
  }
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.004);
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  font-size: calc(14px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
  color: gold;
}
*:before,
*:after {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
  background-color:mediumspringgreen;
}
body{
height:-webkit-fit-content;
height:-moz-fit-content;
height:fit-content;
background-image: url(/portfolio/static/media/rose-blue.611a4bcb.svg);
background-size:cover;
background-attachment: fixed;
background-repeat: repeat;
}
/* border page */
#top, #bottom, #left, #right {
  background: whitesmoke;
  position: fixed;
  z-index:1;
}
#left, #right {
  top: 0; bottom: 0;
  width: 5px;
}
#left { left: 0; }
#right { right: 0; }
  
#top, #bottom {
  left: 0; right: 0;
  height: 5px;
}
#top { top: 0; }
#bottom { bottom: 0; }
/* border page end*/

section:not(.home) {
  position:relative;
  width: inherit; 
  height: 100%;
  margin: 0 50px 50px 50px;
  padding: 70px 50px 50px 50px;
  background-color: rgba(0, 0, 0, 0.4);
   box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
  }
  .content{
    width: 100%;
    margin-top:70px;
  }
  .divider{
    width: 111px;
    display: block;
    border: none;
    border-bottom: 5px solid aquamarine;
    margin:10px auto;  
  }
  h1 {
    font-size: 3em;
    text-align:center;
    font-weight:100;
    font-family: 'Fredericka the Great', cursive;
}
article,p{
  color:white;
  font-size: x-large;
}
h1, h2{
  color: white;
}
h3,
{
  color:#123C69
}
/* animation */

.trademark{
  font-family: 'Cabin Sketch', cursive;
  z-index:1;
}

.puff-in-center {
  -webkit-animation: puff-in-center 0.5s cubic-bezier(0.680, -0.550, 0.265, 1.550) both;
  animation: puff-in-center 0.5s cubic-bezier(0.680, -0.550, 0.265, 1.550) both;
}
/* keyframes */

@-webkit-keyframes puff-in-center {
  0% {
    -webkit-transform: scale(2);
    transform: scale(2);
    -webkit-filter: blur(4px);
    filter: blur(4px);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}
@keyframes puff-in-center {
  0% {
    -webkit-transform: scale(2);
    transform: scale(2);
    -webkit-filter: blur(4px);
            filter: blur(4px);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}


@media screen and (max-width: 768px) {
  #top, #bottom, #left, #right{
    display: none;
  }
  section:not(.home){
  margin:0 auto;
  padding:20px;
  /* height: fit-content; */
  padding-top:30%;
}
h1{
  font-size: 3em;  
  padding:0;
}
.divider{
  display:none;
}
.trademark{
  display: none;
}

  }
